import cx from 'classnames'
import PropTypes from 'prop-types'
import React from 'react'

/**
 * `<MessageGroup />` properly arranges multiple grouped [`<Message />`](./message) components.
 */
const MessageGroup = ({ className, ...props }) => (
  <div className={cx('kds-MessageGroup', className)} {...props} />
)

MessageGroup.propTypes = {
  /** Zero or more [`<Message />`](./message) components */
  children: PropTypes.string.isRequired,
  /** Pass utility classes to the container element */
  className: PropTypes.string
}

export default MessageGroup
